/**
 * EventCategory model events
 */

'use strict';

import {EventEmitter} from 'events';
import EventCategory from './eventCategory.model';
var EventCategoryEvents = new EventEmitter();

// Set max event listeners (0 == unlimited)
EventCategoryEvents.setMaxListeners(0);

// Model events
var events = {
  save: 'save',
  remove: 'remove'
};

// Register the event emitter to the model events
for(var e in events) {
  let event = events[e];
  EventCategory.schema.post(e, emitEvent(event));
}

function emitEvent(event) {
  return function(doc) {
    EventCategoryEvents.emit(event + ':' + doc._id, doc);
    EventCategoryEvents.emit(event, doc);
  };
}

export default EventCategoryEvents;
